.advantage {
  &__title {
    margin: 0 0 32px;
    font-weight: 700;
    font-size: 32px;
    line-height: 32px;
    letter-spacing: -1.5px;
    color: #292929;

    @include onTablet {
      font-size: 40px;
      line-height: 100%;
    }
    
    @include onDesktop {
      font-size: 48px;
      line-height: 48px;
    }
  }
  
  &__subtitle {
    margin: 0 0 16px;
    font-weight: 700;
    font-size: 16px;
    line-height: 140%;
    color: #292929;
  }

  &__paragraph {
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;
    color: #292929;
  }
  
}
