.contact-us {
  &__info {
    margin: 0 0 24px;
  }

  &__info-title {
    margin: 0 0 8px;
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    color: #9fadc2;

  }

  &__info-link {
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
    color: #3e3e3e;
    text-decoration: none;

    &:hover {
      color: #000;
    }
  }

  &__field {
    margin: 0 0 16px;
    
    &--last {
      margin-bottom: 32px;
    }

    &--button {
      margin-bottom: 48px;
    }
  }
}
